<template>
  <div>
    <el-row></el-row>
    <el-row>
      <el-col :span="24">
        <ve-bar :data="chartData" :settings="chartSettings"></ve-bar>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
      this.chartSettings = {
        xAxisType: ['normal', 'percent'],
        xAxisName: ['个数', '正确率'],
        axisSite: {
          top: ['正确率']
        },
         labelMap: {
          'optionA': 'A',
          'optionB': 'B',
          'optionC': 'C',
          'optionD': 'D',
          'optionY': '正确',
          'optionN': '错误',
          'precision': '正确率'
        }
      }
      return {
        chartData: {
          columns: ['issueNum','optionA','optionB','optionC','optionD','optionY','optionN','precision'],
          rows: []
        }
      }
    },
  methods: {
    getData() {
      var api =
        "http://localhost:8888/paper/count/issue/" + this.$route.params.paperID
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.chartData.rows = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

